<template>
  <div class='count-box'>
    <button :disabled='value <= 1' @click='handleSub' class="minus">-</button>
    <input @change='handleChange' class='inp' type="text" :value='value'>
    <button @click='handleAdd' class="add">+</button>
  </div>
</template>

<script>

export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    // 数量 + 1
    handleAdd () {
      this.$emit('input', this.value + 1)
    },
    // 数量 - 1
    handleSub () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    // 数量编辑
    handleChange (element) {
      // 转为数字
      const num = +element.target.value
      // 非法字符或负数
      if (isNaN(num) || num < 1) {
        element.target.value = this.value
        return
      }
      // 改变数量
      this.$emit('input', num)
    }
  }
}
</script>

<style lang='less' scoped>
.count-box{
  width: 110px;
  display: flex;
  .minus, .add{
    width: 30px;
    height: 30px;
    outline: none;
    border: none;
    background-color:#efefef;
  }
  .inp{
    width: 40px;
    height: 30px;
    margin: 0 5px;
    outline: none;
    border: none;
    background-color:#efefef;
    text-align: center;
  }
}
</style>
