import { getAddressList, deleteAddress, addAddress, setDefaultAddress } from '@/api/address'
import { Toast } from 'vant'
export default {
  namespaced: true,
  state () {
    return {
      addressList: []
    }
  },
  mutations: {
    // 设置地址列表
    setAddressList (state, newList) {
      state.addressList = newList
    }
  },
  actions: {
    // 获取收货地址列表数据
    async getAddressAction (context) {
      const { data } = await getAddressList()
      // 更新列表
      context.commit('setAddressList', data.list)
    },
    // 删除地址
    async deleteAddress (context, addressId) {
      // 发送删除请求
      await deleteAddress(addressId)
      Toast('删除成功')
      // 重新渲染
      context.dispatch('getCartAction')
    },
    // 添加地址
    async addAddressList (context, obj) {
      const { name, phone, region, detail } = obj
      await addAddress(name, phone, region, detail)
      Toast('添加成功')
      // 重新渲染
      context.dispatch('getCartAction')
    },
    // 设置默认地址
    async setDefAddress (context, addressId) {
      await setDefaultAddress(addressId)
    }
  },
  getters: {}
}
