import { getInfo, setInfo } from '@/utils/storage'
export default {
  namespaced: true,
  state () {
    return {
      // 个人权限信息
      userInfo: getInfo()
    }
  },
  mutations: {
    // 设置个人权限信息
    setUserInfo (state, userDataObj) {
      state.userInfo = userDataObj
      setInfo(userDataObj)
    }
  },
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  },
  getters: {}
}
