/* 持久化存储模块 */

// 约定通用键名
const INFO_KEY = 'hm_shopping_info' // 用户信息
const HISTORY_KEY = 'hm_history_list' // 搜索历史

// 获取用户信息
export const getInfo = () => {
  const defaultUserInfo = { token: '', userId: '' }
  const result = localStorage.getItem(INFO_KEY)
  return result ? JSON.parse(result) : defaultUserInfo
}

// 设置用户信息
export const setInfo = (userDataObj) => {
  localStorage.setItem(INFO_KEY, JSON.stringify(userDataObj))
}

// 删除用户信息
export const removeInfo = () => {
  localStorage.removeItem(INFO_KEY)
}

// 获取搜索历史
export const getHistoryList = () => {
  const result = localStorage.getItem(HISTORY_KEY)
  return result ? JSON.parse(result) : []
}

// 设置搜索历史
export const setHistoryList = (searchHistoryArray) => {
  localStorage.setItem(HISTORY_KEY, JSON.stringify(searchHistoryArray))
}
