<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <div class='cart-box' v-if="isLogin && cartList.length > 0">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all">共<i>{{ cartTotalNumber || 0 }}</i>件商品</span>
        <span v-if="isEdit" class="all">编辑模式</span>
        <span :class="{ active : isEdit }" class="edit" @click="isEdit = !isEdit">
          <van-icon name="edit" />
          编辑
        </span>
      </div>
      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
          <van-checkbox :value="item.isChecked" @click="toggleCheck(item.goods_id)"></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
              <CountBox :value='item.goods_num' @input="value => changeCount(value, item.goods_id, item.goods_sku_id)"></CountBox>
            </span>
          </div>
        </div>
      </div>
      <!-- 购物车底部 -->
      <div class="footer-fixed">
        <div @click="toggleAllCheck" class="all-check">
          <van-checkbox :value="isAllChecked" icon-size="18"></van-checkbox>
          全选
          <span v-if="cartSelectedNumber > 0">({{ cartSelectedNumber }})</span>
        </div>
        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ cartSelectedPrice }}</i></span>
          </div>
          <div v-if="!isEdit" :class="{disabled: cartSelectedNumber === 0}" @click="goPay" class="goPay">结算</div>
          <div v-else :class="{disabled: cartSelectedNumber === 0}" @click='handleDel' class="delete">删除</div>
        </div>
      </div>
    </div>
    <!-- 购物车为空 -->
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>
</template>

<script>
import CountBox from '@/components/countBox.vue'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  data () {
    return {
      isEdit: false
    }
  },
  computed: {
    // 是否登录
    isLogin () {
      return this.$store.getters.token
    },
    // 辅助函数获取 cart存储组件里的state数据
    ...mapState('cart', ['cartList']),
    // 辅助函数获取 cart存储组件里的gettres数据
    ...mapGetters('cart', ['cartTotalNumber', 'cartSelectedList', 'cartSelectedNumber', 'cartSelectedPrice', 'isAllChecked'])
  },
  created () {
    if (this.isLogin) {
      // 触发cart中的getCartAction
      this.$store.dispatch('cart/getCartAction')
    }
  },
  methods: {
    // 反转 物品选框
    toggleCheck (goodsId) {
      this.$store.commit('cart/toggleCheckedState', goodsId)
    },
    // 反转 全选框
    toggleAllCheck () {
      this.$store.commit('cart/toggleAllCheckedState', !this.isAllChecked)
    },
    // 更新购物车物品数据
    changeCount (value, goodsId, skuId) {
      this.$store.dispatch('cart/updateCountAction', {
        value, // 输入框数据
        goodsId, // 商品id
        skuId // 商品样式id
      })
    },
    // 前往支付页面
    goPay () {
      if (this.cartSelectedNumber) {
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.cartSelectedList.map(item => item.id).join(',')
          }
        })
      }
    },
    // 请求删除选定商品
    async deleteCart () {
      await this.$store.dispatch('cart/deleteSelected')
      this.isEdit = false
    },
    // 删除商品弹窗
    handleDel () {
      if (this.cartSelectedNumber !== 0) {
        this.$dialog.confirm({
          title: '删除商品',
          message: '是否确认从购物车中删除已选定的商品',
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        }).then(() => {
          this.deleteCart()
        }).catch(() => {})
      }
    }
  },
  watch: {
    isEdit (value) {
      if (value) {
        this.$store.commit('cart/toggleAllCheckedState', false)
      } else {
        this.$store.commit('cart/toggleAllCheckedState', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
 // 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
      &.active {
        color: red
      }
    }
  }
  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;
    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}
.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }
  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }
    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}
// 购物车为空
.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}

</style>
