import { getCartList, updateCart, clearCart } from '@/api/cart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: [] // 购物车数据
    }
  },
  mutations: {
    // 设置购物车数据
    setCartList (state, newList) {
      state.cartList = newList
    },
    // 反选“物品勾选框”状态
    toggleCheckedState (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    // 反选“全选物品框”状态
    toggleAllCheckedState (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    // 更新本地购物车物品数据
    updateGoodsCount (state, { goodsId, value }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = value
    }
  },
  actions: {
    // 获取购物车数据
    async getCartAction (context) {
      const { data } = await getCartList()
      // 给list各项添加选中属性
      data.list.forEach(item => { item.isChecked = true })
      // 更新列表
      context.commit('setCartList', data.list)
    },
    // 更新后台购物车物品数据
    async updateCountAction (context, obj) {
      const { goodsId, value, skuId } = obj
      context.commit('updateGoodsCount', {
        goodsId,
        value
      })
      // 发送更新数据请求
      await updateCart(goodsId, value, skuId)
    },
    async deleteSelected (context) {
      const selectedList = context.getters.cartSelectedList
      const selectedIds = selectedList.map(item => item.id)
      // 发送删除请求
      await clearCart(selectedIds)
      Toast('删除成功')
      // 重新渲染
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 获取购物车总数
    cartTotalNumber (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 获取购物车选中物品列表
    cartSelectedList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 获取购物车选中物品总数
    cartSelectedNumber (state, getters) {
      return getters.cartSelectedList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 获取购物车选中物品总价格(保留2位小数)
    cartSelectedPrice (state, getters) {
      return getters.cartSelectedList.reduce((sum, item) => {
        return sum + item.goods_num * item.goods.goods_price_min
      }, 0).toFixed(2)
    },
    // 获取购物车物品是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
